import React,
{
  Component
} from 'react';

const STATE = require('../State.json');

let order;

class OrderItemPage extends Component {

  constructor(props) {
    super(props);

    this.state = {};
  }

  componentWillMount() {
    let orders = [...STATE.orders];
    let itemId = this.props.params.itemId;

    order = orders.find(order => order._id === itemId);

    console.log("order: ", order);
  }


  render() {
    if (order) {
      let cart = order.cart.map(item => {
        return (
          <div className="item" key={item._id}>

            : <span>{item.name}</span> |
            : <span>{item.price}</span> |
            : <span>{item.quantity}</span> |
          </div>
        );
      });

      return (
        <div className="OrderItemPage">
          <h3>  {this.props.params.itemId}, : {order.status}</h3>

          <div className="client">
            <div>: {order.user.name}</div>
            <div>: {order.user.address}</div>
            <div>: {order.user.phone}</div>
            <div>  : {order.user.info}</div>
          </div>

          <div className="products">
            {cart}
          </div>
        </div>
      );
    } else {
      return (
        <div> {this.props.params.itemId}  .</div>
      );
    }
  }
}

export default OrderItemPage;



